import React, { Component } from 'react';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import FormControl from '@material-ui/core/FormControl';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import { PersonalSelect } from './Select';
import TextField from '@material-ui/core/TextField';

import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import Paper from '@material-ui/core/Paper';
import withStyles from '@material-ui/core/styles/withStyles';
import { withFirebase } from './Firebase';
import { compose } from 'recompose';
import { Link, withRouter } from 'react-router-dom';


const styles = theme => ({
  main: {
    width: 'auto',
    display: 'block', // Fix IE 11 issue.
    marginLeft: theme.spacing.unit * 3,
    marginRight: theme.spacing.unit * 3,
    [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
      width: 1000,
      marginLeft: 'auto',
      marginRight: 'auto',
    },
  },
  paper: {
    marginTop: theme.spacing.unit * 8,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    padding: `${theme.spacing.unit * 2}px ${theme.spacing.unit * 3}px ${theme.spacing.unit * 3}px`,
  },
  avatar: {
    margin: theme.spacing.unit,
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing.unit,
  },
  submit: {
    marginTop: theme.spacing.unit * 3,
  }
});


const INITIAL_STATE = {
  firstName: '',
  lastName: '',
  email: '',
  password: '',
  confirmPassword: '',
  type: 'Advocate',
  error: null,
};

const ERROR_CODE_ACCOUNT_EXISTS = 'auth/email-already-in-use';

const ERROR_MSG_ACCOUNT_EXISTS = `
  An account with this E-Mail address already exists.
  Try to login with this account instead. If you think the
  account is already used from one of the social logins, try
  to sign in with one of them. Afterward, associate your accounts
  on your personal account page.
`;
// if firebase error, disable submit button


class SignUpFormBase extends Component {
  constructor(props) {
    super(props);

    this.state = { ...INITIAL_STATE };
  }

  onSubmit = event => {
    event.preventDefault();

    console.log("inside event")
    const { email, password, firstName, lastName, type } = this.state;
    console.log("current state: " + this.state)
    this.props.firebase
      .doCreateUserWithEmailAndPassword(email, password)
      .then(authUser => {
        // Create a user in your Firebase realtime database
        return this.props.firebase
          .user(authUser.user.uid)
          .set({
            firstName,
            lastName,
            email,
            type,
          });
      })
      .then(() => {
        this.setState({ ...INITIAL_STATE });
        this.props.history.push('/advocate');
      })
      .catch(error => {
        this.setState({ error });
      });

  };

  onChange = event => {
    console.log(event.target.name + "   " + event.target.value)
    this.setState({ [event.target.name]: event.target.value });
  };
  
  render() {
    const { classes } = this.props;
    const {
      firstName,
      lastName,
      email,
      password,
      confirmPassword,
      type,
      error,
    } = this.state;

    const isInvalid =
      password !== confirmPassword ||
      password === '' ||
      email === '' ||
      firstName === '' ||
      lastName === '';

    return (
      <main className={classes.main}>
        <CssBaseline />
        <Paper className={classes.paper}>
          <h3>Create Profile</h3>
          <form className={classes.form} onSubmit={this.onSubmit}>
            <FormControl margin="normal" fullWidth>
              <InputLabel htmlFor="name">Phone Number</InputLabel>
              <Input id="phone" name="phone" required autoComplete="phone" onChange={this.onChange} autoFocus />
            </FormControl>
            <PersonalSelect />
              <InputLabel>Personal Statement</InputLabel>
              <TextField
                id="outlined"
                className={classes.story}
                multiline
                fullWidth
                defaultValue="Tell your story."
                margin="normal"
                variant="outlined"
              />
            <Button 
              type="submit"
              fullWidth
              variant="contained"
              color="primary"   
              className={classes.submit}
            >
              Continue
            </Button>
            {error && <p>{error.message}</p>}
          </form>
        </Paper>
      </main>
    );
  }
}

const SignUpForm = compose(
  withRouter,
  withStyles(styles),
  withFirebase,
)(SignUpFormBase);


export default SignUpForm;